.contact_section-dark {
  margin: 5rem 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;

  a {
    text-decoration: none;
    color: lighten(#282c34, 60);
    &:hover {
      color: #089eec;
      text-decoration: underline;
    }
  }
  .contact_title {
    width: 100%;
    font-size: 5rem;
    text-align: center;
    color: #089eec;
    font-weight: 400;
  }
  .contact_pretext {
    margin-top: 2rem;
  }
  .download-link {
    color: #089eec;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  .contact_text-colored {
    color: #089eec;
  }
  .contact_text {
    justify-content: space-around;
    width: 80%;
    flex-wrap: wrap;
    background-color: lighten(#282c34, 4);
    border-radius: 1rem;
    padding: 2rem;
    color: lighten(#282c34, 60);
    font-size: 2rem;
    line-height: 2.6rem;
    ul {
      list-style: none;
    }
  }
}
// light
.contact_section-light {
  margin: 5rem 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  a {
    text-decoration: none;
    color: black;
    &:hover {
      color: #0077b5;
      text-decoration: underline;
    }
  }
  .contact_title {
    width: 100%;
    font-size: 5rem;
    text-align: center;
    color: white;

    font-weight: 400;
  }

  .contact_text-colored {
    color: #0077b5;
  }
  .contact_pretext {
    margin-top: 2rem;
  }
  .download-link {
    color: #0077b5;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
  .contact_text {
    justify-content: space-around;
    width: 80%;
    flex-wrap: wrap;
    background-color: white;
    border-radius: 1rem;
    padding: 2rem;
    color: #282c34;
    font-size: 2rem;
    line-height: 2.6rem;
    ul {
      list-style: none;
    }
  }
  // -webkit-box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
  // -moz-box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
  // box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
}
@media (max-width: 600px) {
  .contact_section-dark {
    .contact_text {
      width: 90%;
    }
  }
}
@media (max-width: 600px) {
  .contact_section-light {
    .contact_text {
      width: 90%;
    }
  }
}
