@import url("https://fonts.googleapis.com/css?family=Lato:300,400|Poppins:300,400,800&display=swap");

.body-light {
  background-color: #0077b5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .icons {
    position: absolute;
    display: flex;
    flex-direction: column;
    top: 2rem;
    right: 2rem;
    background-color: white;

    // box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.25),
    //   0 0.7rem 0.7rem rgba(0, 0, 0, 0.22);
    border-radius: 1rem;
    padding: 1rem;
  }
  .icon {
    font-size: 4rem;
    color: #0077b5;
    transition: all 0.4s;
    margin: 2rem 2rem;
    &:hover {
      transform: scale(1.1);
      cursor: pointer;
    }
  }
  .mode-select {
    position: absolute;
    display: flex;
    align-items: center;
    top: 2rem;
    left: 2rem;
    background-color: white;

    // box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.25),
    //   0 0.7rem 0.7rem rgba(0, 0, 0, 0.22);
    border-radius: 1rem;
  }

  .icon-sun {
    font-size: 3rem;
    // color: #0077b5;
    color: yellow;
    filter: drop-shadow(0px 0px 2px rgba(0, 0, 0, 0.75));
    transition: all 0.4s;
    margin: 2rem;
    &:hover {
      transform: scale(1.05);
      cursor: pointer;
    }
  }
  .icon-moon {
    font-size: 2rem;
    color: lighten(#282c34, 20);

    transition: all 0.4s;
    margin: 2rem;
    &:hover {
      color: #90aa1a;
      transform: scale(1.1);
      cursor: pointer;
    }
  }
  .to_top_button {
    background-color: white;
    border: solid #0077b5 1px;
    color: #0077b5;
    font-size: 2.5rem;
    height: 5rem;
    width: 5rem;
    border-radius: 50%;
    transition: all 0.3s;
    position: fixed;
    right: 5rem;
    bottom: 5rem;

    outline: none;
    cursor: pointer;
    display: block;
    z-index: 10;

    &:hover {
      transform: scale(1.05);
    }
  }
}
.body-dark {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .icons {
    position: absolute;
    display: flex;
    flex-direction: column;
    top: 2rem;
    right: 2rem;
    background-color: lighten(#282c34, 4);

    border-radius: 1rem;
    padding: 1rem;
  }
  .icon {
    font-size: 4rem;
    color: white;
    transition: all 0.4s;
    margin: 2rem 2rem;

    &:hover {
      transform: scale(1.1);
      cursor: pointer;
    }
  }
  .mode-select {
    position: absolute;
    display: flex;
    align-items: center;
    top: 2rem;
    left: 2rem;
    background-color: lighten(#282c34, 4);

    border-radius: 1rem;
  }
  .icon-sun {
    font-size: 2rem;
    color: lighten(#282c34, 35);
    transition: all 0.4s;
    margin: 2rem;
    &:hover {
      color: lighten(#282c34, 55);
      transform: scale(1.1);
      cursor: pointer;
    }
  }
  .icon-moon {
    font-size: 3rem;
    //color: #fcf94f;
    color: yellow;
    transition: all 0.4s;
    margin: 2rem;
    &:hover {
      transform: scale(1.05);
      cursor: pointer;
    }
  }
  .to_top_button {
    background-color: #089eec;
    color: white;
    font-size: 2.5rem;
    height: 5rem;
    width: 5rem;
    border-radius: 50%;
    transition: all 0.3s;
    position: fixed;
    right: 5rem;
    bottom: 5rem;
    border: none;
    outline: none;
    cursor: pointer;
    display: block;
    z-index: 10;

    &:hover {
      transform: scale(1.05);
    }
  }
}

hr {
  width: 90%;
  //border: solid 1px #0077b5;
  opacity: 0;
}

@media (max-width: 1024px) {
  .body-dark {
    padding-top: 15rem;

    .mode-select {
      flex-direction: column;
    }
    .icons {
      flex-direction: row;
    }
  }
}
@media (max-width: 1024px) {
  .body-light {
    padding-top: 15rem;

    .mode-select {
      flex-direction: column;
    }
    .icons {
      flex-direction: row;
    }
  }
}
@media (min-width: 1440px) {
  .body-dark {
    padding-top: 5rem;
  }
}
@media (min-width: 1440px) {
  .body-light {
    padding-top: 5rem;
  }
}
