.about_section-dark {
  margin: 5rem 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;

  .about_text {
    justify-content: space-around;
    width: 80%;
    flex-wrap: wrap;
    background-color: lighten(#282c34, 4);
    border-radius: 1rem;
    padding: 2rem;
    color: lighten(#282c34, 60);
    font-size: 2rem;
    line-height: 2.6rem;
  }
  .about_title {
    width: 100%;
    font-size: 5rem;
    text-align: center;
    color: #089eec;
    font-weight: 400;
  }
}
//light
.about_section-light {
  margin: 5rem 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;

  .about_text {
    justify-content: space-around;
    width: 80%;
    flex-wrap: wrap;
    background-color: white;
    border-radius: 1rem;
    padding: 2rem;
    color: #282c34;
    font-size: 2rem;
    line-height: 2.6rem;
    // -webkit-box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
    // -moz-box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
    // box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
  }
  .about_title {
    width: 100%;
    font-size: 5rem;
    text-align: center;
    color: white;
    // text-shadow: 2px 2px 2px rgb(70, 70, 70);
    font-weight: 400;
  }
}
@media (max-width: 600px) {
  .about_section-dark {
    .about_text {
      width: 90%;
    }
  }
}

@media (max-width: 600px) {
  .about_section-light {
    .about_text {
      width: 90%;
    }
  }
}
