// dark mode
.chat-dark {
  position: relative;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  width: 70rem;
  max-width: 90%;
  min-height: 55rem;
  background-color: lighten(#282c34, 4);
  border-radius: 2rem;
  padding: 1.5rem;
  padding-top: 6.5rem;
  margin-top: 10rem;

  .avatar-image {
    height: 16rem;
    width: 16rem;
    border-radius: 50%;
    position: absolute;
    top: -8rem;
    left: 8rem;
  }
  .arrow {
    // color: lighten(#282c34, 30);
    color: white;
    font-size: 2.4rem;
    position: absolute;
    opacity: 1;
    transition: 0.3s all;
    left: -3.2rem;
  }
  .typing {
    padding: 1.2rem;
    background-color: #fff;
    //border-radius: 0.8rem;
    border-radius: 1rem;
    margin: 0.6rem;
    //color: #282c34;
    color: white;
    opacity: 0.8;
  }
  .my-chat {
    margin: 0.6rem;
    background-color: #fff;
    border-radius: 0.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #282c34;
    border: 1px #282c34 solid;
    font-size: 1.8rem;
    font-weight: 500;
    padding: 1.5rem;
    width: auto;
    max-width: 80%;
  }

  .answer {
    margin: 0.6rem;

    // background-color: rgb(63, 63, 202);
    // background-color: #0077b5;
    background-color: #089eec;

    color: #282c34;

    border-radius: 0.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.8rem;
    font-weight: 500;
    padding: 1.5rem;
    width: auto;
    transition: all 0.3s;
    position: relative;

    &:hover {
      -webkit-box-shadow: 2px 2px 3px 0px rgba(0, 0, 0, 0.75);
      -moz-box-shadow: 2px 2px 3px 0px rgba(0, 0, 0, 0.75);
      box-shadow: 2px 2px 3px 0px rgba(0, 0, 0, 0.75);
      transform: scale(1.05);
      color: white;

      cursor: pointer;
      .arrow {
        opacity: 0;
      }
    }
  }
}
//universal for both modes
