// css tranistion animation

.my-pop-enter {
  opacity: 0;
  transform: scale(0.7);
}
.my-pop-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 400ms ease-in-out;
}

.my-pop-exit {
  opacity: 1;
  transform: scale(1);
}
.my-pop-exit-active {
  opacity: 0;
  transition: all 200ms ease-in-out;
}

// bubble scaling animation

.scaling {
  animation: scaling 0.5s;
  -webkit-animation: scaling 0.5s;
  -moz-animation: scaling 0.5s;
  -ms-animation: scaling 0.5s;
  animation-direction: alternate;
  animation-iteration-count: infinite;
}

@-webkit-keyframes scaling {
  0% {
    transform: translateX(3px);
  }
  100% {
    transform: translateX(0px);
  }
}
@-moz-keyframes scaling {
  0% {
    transform: translateX(3px);
  }
  100% {
    transform: translateX(0px);
  }
}
@keyframes scaling {
  0% {
    transform: translateX(3px);
  }
  100% {
    transform: translateX(0px);
  }
}
